export default function CharacterSelect() {
  const router = useRouter();
  const { setCharacter } = useGameState();

  const onSelect = (char: CharacterClass) => {
    try {
      setCharacter(char);
      router.push("/map");
    } catch (error) {
      console.error("Character selection error:", error);
    }
  };

  return (
    <div className="max-w-6xl mx-auto">
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {characters.map((char) => (
          <Card key={char.name} className="hover:shadow-lg transition-shadow cursor-pointer group">
            <CardHeader>
              <CardTitle className="text-xl font-bold text-center">{char.name}</CardTitle>
              <CardDescription className="text-center">{char.description}</CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="grid grid-cols-2 gap-2 text-sm">
                <div>Health: {char.baseHealth}</div>
                <div>Mana: {char.baseMana}</div>
                <div>Strength: {char.baseStrength}</div>
                <div>Dexterity: {char.baseDexterity}</div>
                <div>Vitality: {char.baseVitality}</div>
                <div>Energy: {char.baseEnergy}</div>
              </div>
              <Button 
                onClick={() => onSelect(char)} 
                className="w-full group-hover:bg-primary/90"
              >
                Select {char.name}
              </Button>
            </CardContent>
          </Card>
        ))}
      </div>
    </div>
  );
}
